@import '../custom/text-styles.scss';
@import '../custom/colors.scss';
.custom {
  max-width: 100%;
  padding: 0px;
  margin: 0px;

  .custom-container {
    align-items: left;
    text-align: left;
    box-shadow: 0 0 1.5rem rgba(105, 105, 105, 0.5);
    background: white;
    margin-top: 3em;
    width: 85%;
    max-width: 1020px;
    height: auto;
    max-height: 390px;
    .main-image-col {
      padding: 0px;
      max-width: 390px;
      margin: 0px;
      img {
        max-height: 390px;
        max-width: 390px;
        padding: 0px;
      }
    }

    .description-col {
      padding: 2em;
      margin: 0px;
      .main-logo-row {
        padding-bottom: 1em;
      }
      .italic-comment-row {
        padding-bottom: 1em;
        span {
          font-style: italic;
          font-weight: 450;
        }
      }
      .author-row-bolt {
        font-size: 0.9em;
        font-weight: 600;
      }
      .author-position-row {
        font-size: 0.8em;
        font-weight: 500;
      }
      .link-right-corner-below {
        display: block;
        text-align: right;
        position: absolute;
        bottom: 1em;
        right: 1em;
      }
    }
  }
}

.flickity-enabled {
  position: relative;
}

.flickity-enabled:focus {
  outline: none;
}

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

/* draggable */

.flickity-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
  -tap-highlight-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

/* ---- flickity-button ---- */

.flickity-button {
  position: absolute;
  background: none;
  border: none;
}

.flickity-button:hover {
  cursor: pointer;
}

.flickity-button:focus {
  outline: none;
}

.flickity-button:active {
  opacity: 0.6;
}

.flickity-button:disabled {
  opacity: 0.3;
  cursor: auto;
  /* prevent disabled button from capturing pointer up event. #716 */
  pointer-events: none;
}

.flickity-button-icon {
  fill: #333;
}

/* ---- previous/next buttons ---- */

.flickity-prev-next-button {
  top: 50%;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  /* vertically center */
  transform: translateY(-50%);
}

.flickity-prev-next-button.previous {
  left: 10px;
}
.flickity-prev-next-button.next {
  right: 10px;
}
/* right to left */
.flickity-rtl .flickity-prev-next-button.previous {
  left: auto;
  right: 10px;
}
.flickity-rtl .flickity-prev-next-button.next {
  right: auto;
  left: 10px;
}

.flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}

/* ---- page dots ---- */

.flickity-page-dots {
  position: absolute;
  width: 100%;
  bottom: -60px;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}

.flickity-rtl .flickity-page-dots {
  direction: rtl;
}

.flickity-page-dots .dot {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin: 0 8px;

  background: #333;
  border-radius: 50%;
  opacity: 0.25;
  cursor: pointer;
}

.flickity-page-dots .dot.is-selected {
  opacity: 1;
  background: $slackPurple;
}
